import React, { useState } from "react";
import Lightbox from "react-image-lightbox";
import "react-image-lightbox/style.css"; // Import the styles for the lightbox

const ImageGallery = ({ images }) => {
  const [lightboxOpen, setLightboxOpen] = useState(false);
  const [photoIndex, setPhotoIndex] = useState(0);

  const openLightbox = () => {
    setLightboxOpen(true);
  };

  const closeLightbox = () => {
    setLightboxOpen(false);
  };

  return (
    <div>
      {/* Display the first image as the header picture */}
      <img
        src={images[0].childImageSharp.gatsbyImageData.src}
        alt={`Gallery Image 1`}
        onClick={openLightbox}
        style={{ cursor: "pointer" }}
      />

      {/* Lightbox component */}
      {lightboxOpen && (
        <Lightbox
          mainSrc={images[photoIndex].childImageSharp.gatsbyImageData.src}
          nextSrc={
            images[(photoIndex + 1) % images.length].childImageSharp.gatsbyImageData
              .src
          }
          prevSrc={
            images[
              (photoIndex + images.length - 1) % images.length
            ].childImageSharp.gatsbyImageData.src
          }
          onCloseRequest={closeLightbox}
          onMovePrevRequest={() =>
            setPhotoIndex((photoIndex + images.length - 1) % images.length)
          }
          onMoveNextRequest={() =>
            setPhotoIndex((photoIndex + 1) % images.length)
          }
        />
      )}
    </div>
  );
};

export default ImageGallery;
